@charset "UTF-8";
.custom-file-input ~ .custom-file-label::after {
  content: "Buscar";
}

.custom-file-input {
  height: 150px;
  width: 100%;
}

.custom-file-label {
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.custom-file {
  margin-bottom: 20px;
}

@media (min-width: 768px) {
  .custom-file-input {
    height: 200px;
  }
  .custom-file-label {
    height: 200px;
  }
}
.dropzone {
  border: 2px dashed #007bff;
  border-radius: 5px;
  background: #f8f9fa;
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.dropzone:hover {
  background-color: #e2e6ea;
}

.dropzone.dragover {
  background-color: #d0d7dd;
}

body {
  padding: 20px;
}

.image-upload {
  display: flex;
  gap: 20px;
  justify-content: space-between;
  margin-bottom: 20px;
}
.image-upload .upload-box {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px dashed #ccc;
  padding: 20px;
  cursor: pointer;
  position: relative;
}
.image-upload .upload-box i {
  font-size: 3rem;
  color: #aaa;
  margin-bottom: 10px;
}
.image-upload .upload-box input[type=file] {
  position: absolute;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.image-upload .upload-box img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  /* Ajusta la imagen para que se ajuste dentro del contenedor */
  display: block;
}

@media (max-width: 768px) {
  .image-upload {
    flex-direction: column;
  }
  .image-upload .upload-box {
    margin-bottom: 10px;
  }
}
/* Estilos para el modal del formulario de actualizar */
.card-body {
  position: relative;
  height: auto;
}

.image-upload {
  max-width: 100%;
  display: flex;
  justify-content: space-around;
  position: relative;
}
.image-upload div {
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: center;
  max-width: 30%;
  min-width: 200px;
}

.img-thumbnail,
.upload-box {
  margin: auto;
}

.owl-nav,
.owl-dots {
  display: none !important;
}

.dataTables_wrapper .dataTables_paginate .paginate_button:hover {
  border: none;
  background: none;
  /* Elimina el fondo en hover */
}

.dataTables_wrapper .dataTables_paginate .paginate_button:active {
  border: none;
  background: none;
  /* Elimina el fondo en active */
}

.upload-box {
  height: 280px !important;
  background-color: azure;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  object-fit: cover !important;
}
.upload-box img {
  width: 100%;
  height: 100%;
}

.image-container {
  position: relative;
}
.image-container .image-popup .card-img {
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.13);
}

.gallery-inner {
  margin: 10px 0 0 0;
  display: flex;
  justify-content: center;
  border-radius: 5px;
}
.gallery-inner .gallery-item {
  border-radius: 5px;
  background-color: #28a745;
  max-height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  flex-wrap: wrap;
  padding: 0;
}
.gallery-inner .gallery-item img {
  height: 350px;
  max-height: 100%;
  object-fit: cover;
  display: block;
}
.gallery-inner .gallery-item:hover {
  box-shadow: 0px 0px 15px rgba(65, 0, 245, 0.397);
  outline: solid 5px rgba(47, 0, 255, 0.349);
  transition: all 0.2s;
}

.image-actions {
  position: absolute;
  top: 10px;
  right: 5px;
  display: none;
  z-index: 10;
}
.image-actions .btn {
  padding: 4px;
  font-size: 26px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.2rem;
  transition: all 0.8s ease-in-out;
}
.image-actions .btn i {
  font-size: 16px;
}
.image-actions .btn-edit {
  background-color: #28a745;
}
.image-actions .btn-edit:hover {
  background-color: #14a333;
}
.image-actions .btn-delete {
  background-color: #dc3545;
}
.image-actions .btn-delete:hover {
  background-color: #c82333;
}

.image-container:hover .image-actions {
  display: flex;
  flex-direction: row;
}

@media (max-width: 768px) {
  .gallery-inner .gallery-item {
    width: 100%;
    height: 300px;
  }
  .gallery-inner .gallery-item img {
    height: 320px;
  }
  .image-actions {
    top: 5px;
    right: 5px;
  }
  .image-actions .btn {
    padding: 6px;
    font-size: 12px;
  }
  .image-actions i {
    font-size: 14px;
  }
}
@media (max-width: 576px) {
  .image-actions .btn {
    padding: 5px;
    font-size: 10px;
  }
  .image-actions i {
    font-size: 12px;
  }
  .gallery-inner .gallery-item {
    width: 100%;
    height: 100px;
  }
  .gallery-inner .gallery-item img {
    height: 120px;
  }
}
.galeria .card {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  border-radius: 10px;
  position: relative;
  max-height: 500px;
  overflow: hidden;
}
.galeria .card .carouselImage {
  border-radius: 10px;
  overflow: hidden;
}
.galeria .card .carouselImage .carousel-inner {
  border-radius: 10px 10px 0 0;
}
.galeria .card .carouselImage .carousel-inner .carousel-item {
  height: 300px;
}
.galeria .card .carouselImage .carousel-control-prev:hover {
  background: rgb(0, 0, 0);
  background: linear-gradient(90deg, rgb(0, 0, 0) 0%, rgb(0, 0, 33) 15%, rgba(0, 212, 255, 0) 100%);
}
.galeria .card .carouselImage .carousel-control-next:hover {
  background: rgb(0, 0, 0);
  background: linear-gradient(270deg, rgb(0, 0, 0) 0%, rgb(0, 0, 33) 15%, rgba(0, 212, 255, 0) 100%);
}
.galeria .card .carouselImage img {
  height: 350px !important;
  object-fit: cover !important;
  /* Ajusta la imagen para que se ajuste dentro del contenedor */
}
.galeria .card .portada-text {
  width: 100%;
  height: 100%;
  background-color: aliceblue;
  box-shadow: 0px 0px 20px rgb(0, 0, 0);
  position: relative;
  z-index: 1000;
}
.galeria .card .portada-text .card-title {
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.301);
  right: 0;
  position: absolute;
  bottom: 0;
  padding: 20px;
}
.galeria .card .portada-text .card-title:hover {
  text-shadow: 1px 1px 2px black, 0 0 1em blue, 0 0 0.2em rgb(0, 0, 0);
}
.galeria .card .img-count {
  position: absolute;
  z-index: 1000;
  bottom: 20px;
  left: 0;
}
.galeria .card .img-count .btn {
  border: none !important;
  border-radius: 0 !important;
  line-height: 1.2;
}
.galeria .card:hover {
  box-shadow: 0 0 1em rgba(0, 0, 255, 0.418), 0 0 0.2em rgba(0, 0, 0, 0.274);
  filter: saturate(180%);
}
.galeria-post {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-height: 400px;
  overflow: hidden;
}

.card-text {
  display: -webkit-box;
  /* Limitar a 2 líneas */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  max-height: 3.6em;
  /* Aproximadamente 2 líneas de texto */
  line-height: 1.8em;
  /* Ajusta la altura de la línea */
}